export default {
	data() {
		return {
			visible: -1,
			token: "",
			surveyArr: [],
			sinArray: [], //单选
			muArray: [], //多选
		}
	},
	onLoad() {
		var that = this;
		var serverUrl = that.serverUrl
		that.token = uni.getStorageSync('token');
		if (that.token == '' || that.token == undefined) {
			uni.redirectTo({
				url: "../Authorization/Authorization"
			})
		} else {
			uni.showLoading({
				title: '加载中'
			});
			uni.request({
				url: serverUrl + '/questionnaire/selectByMeet', //获取微信授权信息
				method: "GET",
				header: {
					'Content-Type': 'application/x-www-form-urlencoded',
					'token': that.token
				},
				success: (res) => {
					console.log(res)
					uni.hideLoading();
					if (res.data.result == '您已填写过问卷') {
						that.visible = 0;
					} else {
						that.visible = 1;
						that.surveyArr = res.data.result;
					}
				},
				fail(res) {
					uni.showToast({
						title: "查询失败，请联系管理员",
						icon: 'none',
					})
				}
			})
		}


	},
	methods: {
		formSubmit: function(e) {
			var that = this;
			var serverUrl = that.serverUrl
			var token = uni.getStorageSync('token');
			var resultId = uni.getStorageSync('resultId');
			// console.log(e.detail.value)
			var NumBase = e.detail.value
			for (var key in NumBase) {
				// console.log(key + " " + NumBase[key])
				if (NumBase[key] == "") {
					uni.showToast({
						title: "有未答题目",
						icon: 'none',
					})
					return false;
				} else {
					var selectVaule = JSON.stringify(e.detail.value);
				}
			}
			console.log(selectVaule)
			uni.request({
				url: serverUrl + '/questionnaireRecord/insert', //获取微信授权信息
				method: "POST",
				data: {
					meetingId: '',
					memberId: '',
					recordJson: selectVaule,
				},
				header: {
					'Content-Type': 'application/x-www-form-urlencoded',
					'token': token
				},
				success: (res) => {
					uni.redirectTo({
						url: "../submit/submit"
					})

				},
				fail(res) {
					uni.showToast({
						title: "失败重试",
						icon: 'none',
					})
				}
			})
		},
	},
}
